import React, { useMemo, useCallback, memo, useState, FC, PropsWithChildren } from 'react'
import { WalletsModalForEth } from '@reef-knot/connect-wallet-modal'
import { useThemeToggle } from '@lidofinance/lido-ui'

import { ModalContext } from './context'
import { WALLET_MODAL } from './enums'
import { ProviderWalletModalProps } from './types'
import { WalletModal } from './walletModal'

export const ProviderWalletModal: FC<PropsWithChildren<ProviderWalletModalProps>> = memo(
  ({ children, walletsMetrics, hiddenWallets }) => {
    const [active, setActive] = useState<WALLET_MODAL | null>(null)
    const { themeName } = useThemeToggle()

    const openModal = useCallback((wModal: WALLET_MODAL) => {
      setActive(wModal)
    }, [])

    const closeModal = useCallback(() => {
      setActive(null)
    }, [])

    const value = useMemo(
      () => ({
        openModal,
        closeModal,
      }),
      [closeModal, openModal],
    )

    const common = {
      onClose: closeModal,
      shouldInvertWalletIcon: themeName === 'dark',
    }

    return (
      <ModalContext.Provider value={value}>
        {children}
        <WalletModal open={active === WALLET_MODAL.wallet} {...common} />
        <WalletsModalForEth
          open={active === WALLET_MODAL.connect}
          metrics={walletsMetrics}
          // Controlled exception!!!
          // eslint-disable-next-line @typescript-eslint/ban-ts-comment
          // @ts-ignore
          hiddenWallets={hiddenWallets}
          {...common}
        />
      </ModalContext.Provider>
    )
  },
)

ProviderWalletModal.displayName = 'ProviderWalletModal'
