import { useCallback, useContext } from 'react'
import { ModalContext } from './context'
import { WALLET_MODAL } from './enums'

type UseModal = (wModal: WALLET_MODAL) => {
  openModal: () => void
  closeModal: () => void
}

export const useModal: UseModal = (wModal) => {
  const methods = useContext(ModalContext)

  const openModal = useCallback(() => {
    methods.openModal(wModal)
  }, [methods, wModal])

  return {
    openModal,
    closeModal: methods.closeModal,
  }
}
